<template>
  <v-container>
    <v-form ref="form" v-model="valid">
      <v-list v-for="i in list" :key="i.model">
        <v-text-field :label="i.label" v-model="obj[i.model]" v-if="i.type === 1" :rules="numberRules" required></v-text-field>
        <v-text-field :label="i.label" v-model="obj[i.model]" v-if="i.type === 3" :rules="nameRules" required></v-text-field>
        <img-input :label="i.label" v-model="obj[i.model]" v-else-if="i.type === 2"></img-input>
      </v-list>
      <v-btn class="float-right" :disabled="!valid" @click="submit">提交</v-btn>
      <v-btn text class="float-right mx-4">取消</v-btn>
    </v-form>
  </v-container>
</template>

<script>
  import ImgInput from './utils/ImgInput'

  export default {
    components: {ImgInput},

    data: () => ({
      list: [{type: 1, model: 'les', label: '输入温度'}, {type: 2, label: '拍照',model:'pic'},{type:3,model:'res',label:'输入原因'}],
      obj: {},
      valid: true,
      nameRules: [
        v => !!v || '必填',
      ],
      numberRules: [
        v => !!v || '必填',
        v => (/^(-?\d+)(\.\d+)?$/.test(v)) || '请输入数字！',
      ],
    }),
    methods: {
      submit() {
        if(this.$refs.form.validate()){
          console.log(this.obj);
          console.log(this.$store.state.picBase64)
        }
      },
    },
  }
</script>



