<template>
  <v-app>
    <v-container fluid>
      <v-carousel hide-delimiters height="200">
        <v-carousel-item
          v-for="(item,i) in items"
          :key="i"
          :src="item.src"
        ></v-carousel-item>
      </v-carousel>
      <v-row dense class="pt-8">
